/* თითოეულ გვერდზე მარცხნივ მდებარე ნავიგაცია */
.main-nav {
    padding: .5rem 1rem;
    font-family: $font-ALK;

    &__list {}

    &__item {}

    &__btn:link,
    &__btn:visited {
        font-size: 1rem;
        letter-spacing: .1rem;
        color: $color-black;
        padding: .4rem .6rem .4rem 0rem;
        display: block;

        border-bottom: 1px solid currentColor;
        margin-bottom: 1rem;

        transition: color .3s; }

    &__btn:hover,
    &__btn:active, {
        color: $color-white; } }

.active {
    color: $color-white !important; }

/* საკითხების ნავიგაცია */
.article-nav {
    height: auto;
    background-color: $color-teal;
    display: inline-block;
    padding: 1.2em 2em;
    border-radius: 4px;

    transition: opacity .5s;

    &__item {
        list-style-type: decimal;
        margin-left: .8rem;
        font-size: 1.125rem; }

    &__link:link,
    &__link:visited {
        color: $color-black;
        font-family: $font-arial;

        padding: .4rem .6rem .4rem 0rem;
        display: block;

        transition: color .3s; }

    &__link:hover {
        color: $color-white; } }
