.article {
    max-width: 720px;
    margin: 0 auto;
    margin-top: 3rem;
    padding: 1rem;

    @media only screen and (max-width: 1080px) {
        width: 100%; }

    &__header {
        margin-bottom: 1.6rem; }

    &__title {
        margin-bottom: 2rem;
        font-family: $font-ALK;
        font-size: 3rem; } }

.section {
    margin-top: 5rem;
    margin-bottom: 6rem; }

.sub-section {
    margin-top: 6rem; }
